import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ContributionsComponent } from './contributions.component';
import { AddContributionComponent } from './pages/add-contribution/add-contribution.component';
import { EditContributionComponent } from './pages/edit-contribution/edit-contribution.component';

const routes: Routes = [
  { 
    path: '', 
    component: ContributionsComponent
  },
  {
    path: 'add',
    component: AddContributionComponent
  },
  {
    path: ':id/edit',
    component: EditContributionComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ContributionsRoutingModule { }
